<template>
  <div class="desc-left">
    <ul class="tabs clearfix">
      <li
        :class="['tab', { active: activeIndex === index }]"
        v-for="(item, index) in [
          '',
          '',
          '',
          '',
        ]"
        :key="index"
        @click="handleTabShow(index)"
      >
        {{ item }}
      </li>
    </ul>
    <desc-block-item title="">
      <div class="content information">
        <table>
          <tr class="row">
            <td class="column"></td>
            <td class="column">2018.01.06-01.14</td>
            <td class="column"></td>
            <td class="column">5</td>
          </tr>
          <tr class="row">
            <td class="column"></td>
            <td class="column">2018.01.06-01.14</td>
            <td class="column"></td>
            <td class="column">
              <p>5</p>
              <p>5</p>
              <p>5</p>
              <p>5</p>
              <p>5</p>
              <p>5</p>
            </td>
          </tr>
        </table>
      </div>
    </desc-block-item>
    <desc-block-item title="">
      <div class="content introduction">
        <div class="normal entrance"></div>
        <p class="english-link">
          https://en.dami.cn/event/tickets_135693/
        </p>
        <div class="normal show-intro"></div>
        <p></p>
        <p></p>
        <p>
          20303007300203030073002030300730020303007300203030073002030300730020303007300
        </p>
        <p>7</p>
        <p></p>
        <p class="p-img">
          <img :src="imgUrl" alt="" />
        </p>
        <p></p>
        <p></p>
        <p></p>
      </div>
    </desc-block-item>
    <desc-block-item title="">
      <div class="content write-comment">
        <div class="rate-content">
          <div class="rate-selection">
            <div class="text"></div>
            <el-rate
              class="rate"
              v-model="rate"
              show-score
              :colors="['#ff3c1b', '#ff3c1b', '#ff3c1b']"
              @change="handleScoreChange"
            ></el-rate>
          </div>
          <textarea
            placeholder="10-10000"
            cols="30"
            rows="5"
            v-model="content"
          ></textarea>
          <button class="comment-btn" @click="handleToComment"></button>
        </div>
      </div>
      <ul class="comment-list">
        <comment-item
          v-for="comment in itemComments"
          :itemData="comment.comments"
          :userDocs="comment.user_docs"
          :key="comment.comments._id"
        >
        </comment-item>
      </ul>
    </desc-block-item>
  </div>
</template>
<script>
import DescBlockItem from '../desc-block-item'
import CommentItem from './comment-item'
export default {
  name: 'DescLeft',
  components: {
    DescBlockItem,
    CommentItem,
  },
  data() {
    return {
      activeIndex: 0,
      imgUrl: '/img/calendar01.png',
      rate: 0,
      content: '',
    }
  },
  props: {
    itemComments: {
      type: Array,
      defatul: [],
    },
  },
  methods: {
    handleTabShow(index) {
      this.activeIndex = index
    },
    handleScoreChange(score) {
      this.rate = score
    },
    async handleToComment() {
      const { content, rate } = this
      this.$emit('handleToComment', { rate, content }, (isCommented) => {
        if(isCommented) {
          this.rate = 0
          this.content = ''
        }
      })
    },
    async getComments() {
      // const ret = await this.$http.getComments({
      //   data: { id: this.$route.query.id || '' },
      // })
      // if (ret) {
      //   this.comments = ret.data[0].comments
      // }
      this.$emit('getComments')
    },
  },
}
</script>
<style lang="less" scoped>
@colorCCC: #cccccc;
@colorEEE: #eeeeee;
@colorFFF: #ffffff;
@border: 1px solid @colorCCC;
.desc-left {
  width: 874px;
  background: white;
  .content.information {
    margin-top: 20px;
    > table {
      margin-bottom: 20px;
      border-top: 2px solid @colorCCC;
      width: 760px;
      .row:first-child {
        border-top: @border;
      }
      .row {
        border: @border;
        border-left: 0px;
        border-top: 0px;
        background-color: @colorEEE;
        height: auto;

        .column {
          height: auto;
          border-left: @border;
          padding: 15px 0 15px 25px;
          width: 110px;
        }
        .column:nth-child(even) {
          width: 279px;
          background-color: @colorFFF;
          > p {
            margin-bottom: 6px;
          }
        }
      }
    }
  }
  .content.introduction {
    margin-top: 20px;
    margin-left: 86px;
    .normal {
      background: @colorEEE;
      padding: 10px 0;
      text-indent: 10px;
      width: 92%;
    }
    > p.english-link {
      margin: 20px 0;
    }
    > p {
      margin: 12px 0;
      padding-right: 40px;
    }
    > p.p-img {
      text-align: center;
    }
  }
  .content.write-comment {
    margin-left: 86px;
    width: 78%;
    .rate-content {
      display: flex;
      flex-direction: column;
      .rate-selection {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        .rate {
          margin-left: 5px;
        }
      }
    }
    .comment-btn {
      margin-top: 15px;
      width: 150px;
      height: 40px;
      border-radius: 3px;
      outline: none;
      background: @HoverC;
      border: none;
      color: @colorFFF;
      cursor: pointer;
    }
  }
  .comment-list {
    margin-top: 30px;
  }
}
.tabs {
  height: 32px;
  > li {
    height: 100%;
    float: left;
    line-height: 32px;
    padding: 0 15px;
    color: #495060;
    box-sizing: border-box;
    border-top: 2px solid transparent;
    cursor: pointer;
    &:hover {
      color: @HoverC;
    }
    &.active {
      color: @HoverC;
      border-color: @HoverC;
    }
  }
}
</style>
