    }
  }

  .recommend-sell{
    margin-top: 30px;
    background: #ffffff;
    padding: 10px 0 30px 20px;
    .tabs{
      ul{
        /* 2pxborder */
        height: 45px;
        border-bottom: 1px solid #dddee1;
        .tab{
          height: 24px;
          line-height: 20px;
          padding: 10px 15px;
          float: left;
          cursor: pointer;
          &.active{
            border-bottom: 2px solid @HoverC;
          }
          &:hover{
            color: #ff3c1b;
          }
        }
      }
    }

    .hot-children{
      ul{
        width: 100%;
        margin-top: 20px;
        >li{
          float: left;
          width: 16.6%;
          padding: 10px 5px 0;
          box-sizing: border-box;
        }
      }
    }
  }

  .floor-rank{
    width: 1200px;
