<template>
  <div class="container order-confirm-wrapper">
    <m-bread-crumb
      :breads="[
        '1&nbsp;',
        '2&nbsp;',
        '3&nbsp;',
        '4&nbsp;',
      ]"
      activeIndex="1"
    ></m-bread-crumb>
    <order-item title="">
      <div class="item-body">
        
        <span class="button tc" @click="handleShowBuyers"></span>
        <span
          v-for="people in filterTicketBuyers"
          v-show="filterTicketBuyers.length > 0 && !showSelectedBuyer"
          style="margin-left: 15px"
          >{{ people.name }}</span
        >
      </div>
    </order-item>
    <el-dialog title="" :visible.sync="showSelectedBuyer">
      <el-checkbox-group
        v-model="selectedBuyers"
        @change="handleBuyerChange"
        :max="this.seatTableData.length"
      >
        <el-checkbox
          v-for="buyer in ticketBuyers"
          :label="buyer._id"
          :key="buyer._id"
          >{{ buyer.name }}</el-checkbox
        >
      </el-checkbox-group>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose"> </el-button>
        <el-button type="primary" @click="handleSureBuyers"> </el-button>
      </div>
    </el-dialog>
    <order-item title="">
      <div class="item-body pay">
        <input type="radio" checked="checked" />
        <span></span>
      </div>
    </order-item>
    <order-item title="">
      <div class="item-body goods-list">
        <order-table
          :data="seatTableData"
          :border="true"
          :columns="columns"
        ></order-table>
      </div>
    </order-item>
    <order-item title="">
      <div class="item-body bill">
        <div class="selection">
          <span
            :class="['tc', { active: selectedBill === 'company' }]"
            @click="handleSelectBill"
            ></span
          >
          <span
            :class="['tc', { active: selectedBill === 'personal' }]"
            @click="handleSelectBill"
            ></span
          >
        </div>
        <div class="bill-info">
          <el-form
            ref="billForm"
            :model="billForm"
            label-width="80px"
            class="bill-form"
          >
            <el-form-item label="">
              <el-input
                v-model="billForm.companyName"
                style="width:40%"
              ></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input
                v-model="billForm.taxNumber"
                style="width:40%"
              ></el-input>
            </el-form-item>
          </el-form>
          <button class="sure-btn"></button>
        </div>
      </div>
    </order-item>
    <div class="insurance">
      <p class="row">
        <label class="u-checkbox">
          <input type="checkbox" @click="clickInsurance" />
          <span class="txt"
            >20.00 </span
          >
        </label>
      </p>
      <p class="row">
        
        <a href="javascript:;"></a> 
        <a href="javascript:;"></a>
      </p>
      <p class="row"></p>
      <p class="row">
        
        <span class="cell">{{ totalPrice }}</span>
      </p>
      <p v-show="insuranceFlag" class="row">
        
        <span class="cell"> <i>+</i> {{ payMount - totalPrice }}.00</span>
      </p>
    </div>
    <div class="agree">
      <p class="row txt">
        
        <span class="amount"> {{ this.payMount }}</span>
      </p>
      <p class="row">
        <label class="u-checkbox">
          <el-popover
            placement="bottom"
            width="60"
            trigger="manual"
            content=""
            visible-arrow
            v-model="tipVisible"
          >
          </el-popover>
          <input type="checkbox" @click="clickAgree" />
          <span class="txt"
            >
            <el-tooltip placement="top">
              <a href="javascript;;"></a>
              <div slot="content">
                <p>
                  /
                </p>
                <p>/</p>
                <p>
                  /
                </p>
              </div>
            </el-tooltip>
            <el-tooltip placement="top">
              <a href="javascript;;"></a>
              <div slot="content">
                <p>
                  1
                  /
                </p>
                <p>2 </p>
                <p>3 </p>
              </div>
            </el-tooltip>
            <el-tooltip placement="top">
              <a href="javascript;;"></a>
              <div slot="content">
                <p>
                  1
                </p>
                <p>
                  2/
                </p>
              </div>
            </el-tooltip>
          </span>
        </label>
      </p>
      <p class="row"></p>
      <button class="tc" type="primary" @click="submitOrder">
        
      </button>
    </div>
  </div>
</template>
<script>
import MBreadCrumb from '@/components/bread-crumb'
import OrderItem from './order-item'
import OrderTable from '@/components/table'
import { SEAT_ARRAY_KEY } from '@/config'
import { mapState } from 'vuex'
export default {
  name: 'OrderConfirm',
  components: {
    MBreadCrumb,
    OrderItem,
    OrderTable,
  },
  data() {
    return {
      // query
      seatData: [],
      seatTableData: [],
      columns: [
        {
          title: '',
          key: 'itemName',
        },
        {
          title: '',
          key: 'seatPosition',
        },
        {
          title: '',
          key: 'price',
        },
        {
          title: '',
          key: 'num',
        },
        {
          title: '',
          key: 'price',
        },
      ],
      selectedBill: 'company',
      billForm: {
        companyName: '',
        taxNumber: '',
      },
      insuranceFlag: false, // 
      agreeFlag: false, // 
      tipVisible: false,
      showSelectedBuyer: false, // dialog
      selectedBuyers: [],
      ticketBuyers: [],
    }
  },
  methods: {
    handleSelectBill(e) {
      if (e.target && e.target.innerText === '') {
        this.selectedBill = 'company'
      } else if (e.target && e.target.innerText === '') {
        this.selectedBill = 'personal'
      }
    },
    clickInsurance() {
      this.insuranceFlag = !this.insuranceFlag
    },
    clickAgree() {
      this.agreeFlag = !this.agreeFlag
    },
    // 
    async submitOrder() {
      // true
      // 
      if (!this.agreeFlag) {
        this.tipVisible = true
        setTimeout(() => {
          this.tipVisible = false
        }, 3000)
      } else if (this.selectedBuyers.length <= 0) {
        this.$message.error('')
        return
      } else {
        // 
        let invoiceFlag = false
        let invoiceType = this.selectedBill === 'company' ? 0 : 1
        let { companyName, taxNumber } = this.billForm
        if (companyName && taxNumber) {
          invoiceFlag = true
        }
        const ret = await this.$http.postUserOrder({
          cinemaId: this.$route.query.cinemaId,
          isNeedInvoice: invoiceFlag ? 1 : 0,
          companyName: companyName ? companyName : '',
          invoiceType,
          taxNumber: taxNumber ? taxNumber : '',
          isNeedInsurance: this.insuranceFlag ? 1 : 0,
          linkIds: this.selectedBuyers,
          seatData: this.seatData,
          itemId: this.$route.query.itemId,
        })
        if (ret) {
          let orderId = ret.data[0].orderList._id
          console.log("orderId", orderId)
          if(orderId){
            this.$router.push({
              path: '/paymethod',
              query: {
                orderId,
              },
            })
          }
          
        }
      }
    },
    handleBuyerChange(val) {
      console.log('handleBuyerChange::::::::', val)
      if (val.length >= this.seatTableData.length) {
        this.$message.info('')
        return
      } else {
        this.selectedBuyers = val
      }
    },
    // 
    handleSureBuyers() {
      this.showSelectedBuyer = false
    },
    async handleShowBuyers() {
      this.showSelectedBuyer = true
      const ret = await this.$http.getOrderTicketBuyerList()
      if (ret && ret.data && ret.data[0]) {
        this.ticketBuyers = ret.data[0].ticketBuyerList
      }
    },
    handleClose() {
      this.selectedBuyers = []
      this.showSelectedBuyer = false
    },
  },
  mounted() {
    const selected =
      (this.seat.length && this.seat) ||
      JSON.parse(localStorage.getItem(SEAT_ARRAY_KEY))
    console.log('selected', selected)
    this.seatData = selected
    this.seatTableData = selected.map(item => {
      return {
        itemName: item.itemName,
        seatPosition: `${item.x}${item.y}`,
        price: item.price,
        num: 1,
      }
    })
    console.log('this.$route.query.itemId', this.$route.query.itemId)
  },
  computed: {
    totalPrice() {
      return this.seatTableData.reduce((accumulator, cur) => {
        return accumulator + cur.price
      }, 0)
    },
    payMount() {
      return this.insuranceFlag
        ? this.totalPrice + 20 * this.seatTableData.length
        : this.totalPrice
    },
    filterTicketBuyers() {
      const filterTicketBuyers = this.ticketBuyers.filter(item =>
        this.selectedBuyers.includes(item._id)
      )
      return filterTicketBuyers
    },
    ...mapState({
      seat: state => state.seat.selectedArray,
    }),
  },
}
</script>
<style lang="less">
.el-popover {
  min-width: 100px;
  margin-top: 7px;
  margin-left: 0;
}
.el-popover--plain {
  padding: 10px;
  color: red;
}
.order-confirm-wrapper {
  margin-top: 20px;
  background: #fff;
  padding: 10px 20px 0;
  .item-body {
    padding: 10px 0;
    height: 26px;
    line-height: 26px;
    &.pay {
      display: flex;
      align-items: center;
      > input[type='radio']:checked {
        background-color: @HoverC;
        color: @HoverC;
      }
      > span {
        display: inline-block;
        margin-left: 15px;
      }
    }
    .button {
      display: inline-block;
      height: 26px;
      width: 100px;
      background: @HoverC;
      color: #fff;
      border-radius: 4px;
      cursor: pointer;
    }
    &.goods-list {
      height: 100%;
    }
    &.bill {
      height: 100%;
      .selection {
        display: flex;
        > span {
          display: inline-block;
          cursor: pointer;
          width: 60px;
          height: 30px;
          line-height: 30px;
          box-sizing: border-box;
          &.active {
            border-bottom: 1px solid @HoverC;
          }
        }
      }
      .bill-info {
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 20px 0 40px 20px;
        .el-form-item:last-child {
          margin-top: 20px;
        }
        .sure-btn {
          outline: none;
          border: none;
          border-radius: 4px;
          background: @HoverC;
          padding: 10px;
          cursor: pointer;
          color: white;
          font-size: 12px;
          margin: 20px 0 0 10px;
        }
      }
    }
  }
  .insurance {
    border-top: 1px solid #c3c3c3;
    padding-top: 15px;
    height: 91px;
    > p.row {
      text-align: right;
      padding: 3px 0;
      .u-checkbox {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        > input[type='checkbox'] {
          margin-right: 6px;
        }
      }
      > .cell {
        color: @HoverC;
      }
    }
  }
  .agree {
    text-align: right;
    border-top: 1px dotted #c3c3c3;
    padding: 15px 0;
    > p.row {
      padding: 3px 0;
      > .amount {
        color: @HoverC;
      }
      > .u-checkbox {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        > input[type='checkbox'] {
          margin-right: 6px;
        }
      }
    }
    > button {
      margin-top: 12px;
      border: none;
      outline: none;
      background: @HoverC;
      color: white;
      border-radius: 4px;
      padding: 10px 15px;
      cursor: pointer;
    }
  }
}
</style>
